import axios from 'axios'
import {BaseResponse, BaseResponseNoPagination} from 'src/app/models/api.types'
import {Religion} from '../models/Religion'

export const url = `/api/v1/religion`

type ReligionListParam = {
  // page: number
  // limit: number
  search?: string
  statuses?: string[]
  // role_id?: string
  // cancelToken?: any
}

export function createReligion(description: string, status: number, logo: string) {
  return axios.post(url, {description: description, status: status, logo: logo})
}

export function updateReligion(id: string, description: string, status: number, logo: string) {
  return axios.put(url + `/${id}`, {description: description, status: status, logo: logo})
}

export function deleteReligion(id: string) {
  return axios.delete(url + `/${id}`)
}

export function getReligionDetail(id: string) {
  return axios.get<BaseResponse<Religion>>(url + `/${id}`)
}

export function getReligionList({
  search,
  statuses,
}: // role_id,
// cancelToken,
ReligionListParam) {
  // return Array<Religion>
  return axios.get<BaseResponseNoPagination<Religion>>(url, {
    params: {
      search,
      // role_id,
      statuses,
    },
    // cancelToken,
  })
}
