import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {ReligionList} from './page/ReligionList'
import { AddReligion } from './page/AddReligion'
import ReligionScreens from './Screens'

const ReligionRoutes: React.FC = () => {
  return (
    <Switch>
      <Route path={ReligionScreens.RELIGION_LIST.PATH} component={ReligionList} />
      <Route path={ReligionScreens.ADD_RELIGION.PATH} component={AddReligion} />
      <Redirect from='/religion' exact={true} to={ReligionScreens.RELIGION_LIST.PATH} />
      <Redirect to={ReligionScreens.RELIGION_LIST.PATH} />
    </Switch>
  )
}

export default ReligionRoutes
